import React, { useMemo } from "react";
import { Pagination } from "react-bootstrap";
import styles from "./PaginationComponent.module.scss";

type PaginationComponentProps = {
  totalItems: number;
  itemsPerPage: number;
  currentPage: number;
  onPageChange: (pageNumber: number) => void;
};

export const PaginationComponent: React.FC<PaginationComponentProps> = (
  props,
) => {
  const { totalItems, itemsPerPage, currentPage, onPageChange } = props;
  const totalPages = Math.ceil(totalItems / itemsPerPage);

  const handlePageChange = (pageNumber: number) =>
    pageNumber < 0 && pageNumber > totalPages ? null : onPageChange(pageNumber);

  const paginationItems = useMemo(() => {
    return Array.from({ length: totalPages }, (_, index) => {
      const pageNumber = index + 1;
      const isActive = pageNumber === currentPage;

      return (
        <Pagination.Item
          key={pageNumber}
          active={isActive}
          onClick={() => handlePageChange(pageNumber)}
        >
          {pageNumber}
        </Pagination.Item>
      );
    });
  }, [totalPages, currentPage]);

  return (
    <Pagination className={`${styles.custom_pagination}`}>
      <Pagination.First onClick={() => handlePageChange(1)} />
      <Pagination.Prev
        onClick={() =>
          currentPage === 1 ? 1 : handlePageChange(currentPage - 1)
        }
      />
      {paginationItems}
      <Pagination.Next
        onClick={() =>
          currentPage === totalPages
            ? totalPages
            : handlePageChange(currentPage + 1)
        }
      />
      <Pagination.Last onClick={() => handlePageChange(totalPages)} />
    </Pagination>
  );
};
